import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { PackingDto } from 'src/app/shared/dtos/identity/packing-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-packing',
  templateUrl: './create-edit-packing.component.html',
  styleUrls: ['./create-edit-packing.component.scss']
})
export class CreateEditPackingComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

  packing: PackingDto = new PackingDto();
  saving=false;
  itemList=[];
  _id=null;
  isNo=false;

  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Packing No","field":"pck_no"},
    {"title":"Packing Title","field":"pck_title"},
  ];

  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
  }
  getData(){
    this.itemList= [];
    this._processDataService.getData("/api/packs").subscribe(response=>{
      if (response.status === 1) {
        this.itemList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/packs/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.packing = response.data;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/packs/no/" + this.packing.pck_no).subscribe(response=>{
      if(response.status === 1){
        this.packing = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
        this.reset();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/packing").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.packing.pck_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    debugger
    this.saving=true;
    let url_ = "/api/packs";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/packs/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.packing).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{debugger
    this._processDataService.patch(url_,this.packing).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/packs/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }
    });
  }
  reset():void{
    this.packing=new PackingDto();
    this._id=null;
    this.isNo = false;
  }

}


