import { IPhone } from './../../../shared/interface/identity/phone-interface';
import { ChangeDetectorRef, Component, ElementRef, Injector, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { CustomerDto } from 'src/app/shared/dtos/identity/customer-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';
import { IContactDetailDto } from 'src/app/shared/interface/identity/contactdetail-interface';
import { IOfficeDetailDto } from 'src/app/shared/interface/identity/officedetail-interface';
import { ICustomerContact, ICustomerOffice } from 'src/app/shared/interface/identity/customer-interface';
import { ContactDetailComponent } from 'src/app/shared/components/contact-detail/contact-detail.component';

@Component({
  selector: 'app-customer',
  templateUrl: './create-edit-customer.component.html',
  styleUrls: ['./create-edit-customer.component.scss']
})
export class CreateEditCustomerComponent extends AppComponentBase implements OnInit  {
@ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;

@ViewChild('cust_Contact1', { static: true })
customer_ContactList: ContactDetailComponent;


@ViewChild('customer_phoneList', { static: true })
customer_phoneList: ContactDetailComponent;
@ViewChild('customer_contactsList', { static: true })
customer_contactsList: ContactDetailComponent;
@ViewChild('office_contactsList', { static: true })
office_contactsList: ContactDetailComponent;

customerTypeList=["Open-Market", "Individual", "Company"];
customerTermList=['Walk-In', 'No-Contract', 'On-Contract'];
designationList=['Owner', 'Chairman', 'MD', 'President', 'EVP', 'SVP', 'VP', 'GM', 'Director', 'Senior Manager', 'Manager', 'Executive', 'Accountant', 'Clerk', 'Driver', 'Assistant', 'Loader', 'Labor']

customer: CustomerDto= new CustomerDto();
contactDetail:IContactDetailDto={} as IContactDetailDto;
officeDetail:IOfficeDetailDto={} as IOfficeDetailDto;

customerContact:ICustomerContact={} as ICustomerContact;
customerOffice:ICustomerOffice={} as ICustomerOffice;
phone:IPhone={} as IPhone;
cust_phone:IPhone={} as IPhone;
cust_contact:IPhone={} as IPhone;
off_contact:IPhone={} as IPhone;

CustomerList=[];
cityList=[];
companyList=[];

customerCity:any;
contactCity:any;
officeCity:any;
company:any;

saving=false;
_id=null;
isNo = false;
tableHeaders=[
  {"title":"Id","field":"cust_no","hide":true},
  {"title":"Title","field":"cust_title"},
  {"title":"Customer short","field":"cust_short"},
  {"title":"Type","field":"cust_type"}
];
  constructor( injector: Injector,
    private _processDataService:IdentityService,
    private ref: ChangeDetectorRef,
    ) {super(injector) }

  ngOnInit() {
    this.getLocationList();
    this.getCompanyList();
    this.customer.cust_phones=[];
    this.contactDetail.cust_phones=[];
    this.contactDetail.contacts=[];
    this.officeDetail.phones=[];
    this.customer.cust_contacts=[];
    this.customer.cust_offices=[];
    this.customerContact.contact={} as IContactDetailDto;
    this.customerOffice.office={} as IOfficeDetailDto;
    }
  getData(){ 
    this.CustomerList= [];
    this._processDataService.getData("/api/customers").subscribe(response=>{
      debugger
      if (response.message = "Success") {
        this.CustomerList = response.data;
        this.showDataModal.show(this.tableHeaders,this.CustomerList);
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/customers/" + event._id).subscribe(response=>{
      this.customer = response.data;
      this._id=event._id;
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/customer/no/" + this.customer.cust_no).subscribe(response=>{
      if(response.status === 1){
        this.customer = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
        this.reset();
      }
    })
  }
  getNextNo(){
    this._processDataService.getData("/api/nextNumber/customer").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.customer.cust_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    this.saving=true;
    // let url_ = "/api/customers/";
    // if (!isNullOrUndefined(this._id)) {
    //   url_ = "/api/customers/" + this._id;
    // }
    debugger
    this.customer.cust_phones =this.customer_ContactList.contacts;
    this.contactDetail.cust_phones = this.customer_phoneList.contacts;
    this.contactDetail.contacts = this.customer_contactsList.contacts;
    this.officeDetail.phones = this.office_contactsList.contacts;
    this.customerContact.contact = this.contactDetail;
    this.contactDetail.city= this.contactCity._id;
    this.customer.cust_contacts.push(this.customerContact);
    this.customerOffice.office=this.officeDetail;
    this.customerOffice.office.city=this.officeCity._id;
    this.customer.cust_offices.push(this.customerOffice)
    this.customer.cust_city=this.customerCity._id;
    this.customer.cust_comp=this.company._id;
    this.customer.cust_ud=true;
    
    this.saving=true;
    let url_ = "/api/customers";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/customers/id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.customer).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.customer).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/customers/" + this._id).subscribe(result => {
          this.notify.success('Deleted Successfully.', 'Deleted!');
          this.customer=new CustomerDto();
          this._id=null;
        });
      }
    });
  }
  getLocationList(){
    debugger
    this._processDataService.getData("/api/locations/city/list").subscribe(response=>{
      debugger
      if (response.status === 1) {
        response.data.forEach(loc => {
          var item = {id: loc._id,name:loc.loc_title};
          this.cityList.push(item);
      });

      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getCompanyList(){
    debugger
    this._processDataService.getData("/api/companies/list").subscribe(response=>{
      debugger
      if (response.status === 1) {
        response.data.forEach(com => {
          var item = {id: com._id,name:com.comp_title};
          this.companyList.push(item);
      });

      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  setTypeheadList(param){
    debugger
    switch (param) {
        case 'type':
        this.typeheadlist=this.customerTypeList
        break;
        case 'term':
        this.typeheadlist=this.customerTermList;
        break;
        case 'desig':
        this.typeheadlist=this.designationList;
        break;
        case 'city':
          this.typeheadlist=this.cityList;
          break;
          case 'company':
            this.typeheadlist=this.companyList;
            break;
      
    } 
  }
  reset():void{
    this.customer=new CustomerDto();
    this._id=null;
    this.isNo = false;
  }
}
