import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreateEditCustomerComponent } from 'src/app/main/setup/customer/create-edit-customer.component';
import { CreateEditIServicesComponent } from 'src/app/main/setup/iservices/create-edit-iservices.component';
import { CreateEditItemComponent } from 'src/app/main/setup/item/create-edit-item.component';
import { CreateEditPackingComponent } from 'src/app/main/setup/packing/create-edit-packing.component';
import { CreateEditVehicleComponent } from 'src/app/main/setup/vehicle/create-edit-vehicle.component';


const routes: Routes = [
  {
    path:'',
    children:[
      {
        path:'item',
        component:CreateEditItemComponent
      },
      {
        path:'packing',
        component:CreateEditPackingComponent
      },
      {
        path:'services',
        component:CreateEditIServicesComponent
      },
      {
        path:'customer',
        component:CreateEditCustomerComponent
      },
      {
        path:'vehicle',
        component:CreateEditVehicleComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IdentityRoutingModule { }
