import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { merge, Observable, OperatorFunction, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, finalize, map } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { ContactDetailComponent } from 'src/app/shared/components/contact-detail/contact-detail.component';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { VehicleDto } from 'src/app/shared/dtos/identity/vehicle-dto';
import { IVehicleStaffListDto } from 'src/app/shared/interface/identity/vehicle-interface';
import { IVehicleStaffDto } from 'src/app/shared/interface/identity/vehicleStaff-interface';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-voucher',
  templateUrl: './create-edit-vehicle.component.html',
  styleUrls: ['./create-edit-vehicle.component.scss']
})
export class CreateEditVehicleComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  @ViewChild('contactDetail', { static: true }) contactDetail: ContactDetailComponent;

  Vehicle: VehicleDto = new VehicleDto();
  
  typeList=['Own', 'Rental'];
  designationList=['Driver', 'Helper']
  VehicleStaffListDto: IVehicleStaffListDto={} as IVehicleStaffListDto;
  VehicleStaffDto : IVehicleStaffDto={} as IVehicleStaffDto;
  saving=false;
  voucherList=[];
  uoms=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Vehicle No","field":"voucher_no"},
    {"title":"Vehicle Title","field":"voucher_title"},
    {"title":"UOM","field":"voucher_uom"}
  ];
  
  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.getUOM();
  }

  getUOM(){debugger
    this._processDataService.getData("/api/uoms").subscribe(response=>{
      if (response.status === 1) {
        this.uoms = response.data;
        this.typeheadlist=this.uoms;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getData(){
    this.voucherList= [];
    this._processDataService.getData("/api/vehicles").subscribe(response=>{
      if (response.status === 1) {
        this.voucherList = response.data;
        this.showDataModal.show(this.tableHeaders,this.voucherList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getDataById(event){
    this._processDataService.getDataById("/api/vehicles/id/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.Vehicle = response.data;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/vehicles/no/" + this.Vehicle.vh_no).subscribe(response=>{
      if(response.status === 1){
        this.Vehicle = response.data;
        this.contactDetail.contacts=response.data.phones
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.reset();
        this.getNextNo();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/voucher").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.Vehicle.vh_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  
  save(saveForm:NgForm):void{
    debugger
    this.VehicleStaffDto.phones=this.contactDetail.contacts;
    this.VehicleStaffListDto.staff=this.VehicleStaffDto;
    this.Vehicle.vh_staffs= this.VehicleStaffListDto;
    this.saving=true;
    let url_ = "/api/vehicles";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/vehicles/id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
    this.getUOM();
  }
    
  create(url_: string, saveForm:NgForm): void{
    this.contactDetail.contacts
    this._processDataService.create(url_,this.Vehicle).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.Vehicle.vh_no = response.data.vh_no + 1;
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.Vehicle).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }


  
  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/vehicles/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }
    });
  }

  reset():void{
    this.Vehicle=new VehicleDto();
    this._id=null;
    this.isNo = false;
  }
  setTypeheadList(param){
    switch (param) {
        case 'type':
        this.typeheadlist=this.typeList
        break;
        case 'desig':
        this.typeheadlist=this.designationList;
        break;
      
    } 
  }
}


