import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { merge, Observable, OperatorFunction, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, finalize, map } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { ItemDto } from 'src/app/shared/dtos/identity/item-dto';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-create-edit-item',
  templateUrl: './create-edit-item.component.html',
  styleUrls: ['./create-edit-item.component.scss']
})
export class CreateEditItemComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  @ViewChild('instance', {static: true}) instance: NgbTypeahead;
  Item: ItemDto = new ItemDto();
  saving=false;
  itemList=[];
  uoms=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"Item No","field":"item_no"},
    {"title":"Item Title","field":"item_title"},
    {"title":"UOM","field":"item_uom"}
  ];

  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) {super(injector) }

  ngOnInit() {
    this.getUOM();
  }
  getUOM(){
    this._processDataService.getData("/api/uoms").subscribe(response=>{
      if (response.status === 1) {
        this.uoms = response.data;
        this.typeheadlist = this.uoms;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getData(){
    this.itemList= [];
    this._processDataService.getData("/api/items").subscribe(response=>{
      if (response.status === 1) {
        this.itemList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  getDataById(event){
    this._processDataService.getDataById("/api/items/id/" + event._id).subscribe(response=>{
      if (response.status === 1) {
        this.Item = response.data;
        this._id=event._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/items/no/" + this.Item.item_no).subscribe(response=>{
      if(response.status === 1){
        this.Item = response.data;
        this._id = response.data._id;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
        this.reset();
      }
    })
  }

  getNextNo(){
    this._processDataService.getData("/api/nextNumber/item").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.Item.item_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    debugger
    this.saving=true;
    let url_ = "/api/items";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/items/id/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }

  create(url_: string, saveForm:NgForm): void{
    this._processDataService.create(url_,this.Item).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      if (response.status == 1) {
        this.notify.success("Saved Successfully","Success");
        saveForm.resetForm();
        this.reset();
      } else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.Item).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      if (response.status === 1) {
        this.notify.success("Update Successfully","Success");
        saveForm.resetForm();
        this.reset();
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/items/" + this._id).subscribe(result => {
          if (result.status === 1) {
            this.notify.success('Deleted Successfully.', 'Deleted!');
            this.reset();
          }else{
            this.message.fire("Error", result.message, "error");
          }
        });
      }
    });
  }

  reset():void{
    this.Item=new ItemDto();
    this._id=null;
    this.isNo = false;
    this.getUOM();
  }

}


