import { IPhone } from './../../../shared/interface/identity/phone-interface';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { ViewChild } from '@angular/core';
import { Component, Injector, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { debounceTime, distinctUntilChanged, filter, finalize, map } from 'rxjs/operators';
import { AppComponentBase } from 'src/app/shared/common/app-component-base';
import { LookupTableModalComponent } from 'src/app/shared/components/lookup-table-modal/lookup-table-modal.component';
import { IdentityService } from 'src/app/shared/services/identity.service';
import { isNullOrUndefined } from 'util';
import { iServiceHeadDto, iServicesDto } from 'src/app/shared/dtos/identity/iservices-dto';
import { merge, Observable, OperatorFunction, Subject } from 'rxjs';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-create-edit-company',
  templateUrl: './create-edit-iservices.component.html',
  styleUrls: ['./create-edit-iservices.component.scss']
})
export class CreateEditIServicesComponent extends AppComponentBase {
  @ViewChild('showDataModal', { static: true }) showDataModal: LookupTableModalComponent;
  @ViewChild('instance', {static: true}) instance: NgbTypeahead;
  
  invoiceType=['None', 'All', 'LCL', 'FCL', 'Book', 'Delivery', 'Return'];
  item=["None", "New", "Edit", "Post", "Un-Post"];
  autotype=["Auto","Manual"];
  docEvent=["New","Edit","Post"];
  docRule=['None', 'Inter-City-Fare', 'Intra-City-Fare', 'Labor', 'Other'];
  autoMode=['None', 'Auto', 'Manual'];
  iServices: iServicesDto = new iServicesDto();
  service: iServiceHeadDto = {} as iServiceHeadDto;

  saving=false;
  itemList=[];
  _id=null;
  isNo = false;
  tableHeaders=[
    {"title":"Id","field":"_id","hide":true},
    {"title":"IServices No","field":"stf_no"},
    {"title":"IServices Name","field":"stf_name"},
    {"title":"nick","field":"stf_nick"}
  ];
  
  constructor(
    injector: Injector,
    private _processDataService:IdentityService
  ) 
  {
    super(injector)
    }

  ngOnInit() {
    
    this.iServices.isrv_auto_type = 'Auto';
    this.iServices.isrv_doc_event = 'New';
    this.iServices.isrv_heads=[];
    this.typeheadlist= this.item;
  }

  getData(){ 
    debugger
    this.itemList= [];
    this._processDataService.getData("/api/iservices").subscribe(response=>{
      if (response.status === 1) {
        this.itemList = response.data;
        this.showDataModal.show(this.tableHeaders,this.itemList);
      }
    })
  }
  getDataById(event){
    this._processDataService.getDataById("/api/iservices/" + event._id).subscribe(response=>{
      this.iServices = response.data;
      this.iServices.isrv_heads.forEach(element => {
        this.service = element;
      });
      this._id=event._id;
    })
  }
  getDataByNo(){
    this._processDataService.getDataById("/api/iServices/" + this.iServices.isrv_no).subscribe(response=>{
      if(response.status === 1){
        this.iServices = response.data;
        this.iServices.isrv_heads.forEach(element => {
          this.service = element;
        });
      }else{
        this.notify.error('Search item not found','Not Found');
      }
    })
  }
  getNextNo(){
    this._processDataService.getData("/api/nextNumber/iservice").subscribe(response=>{
      if (response.status === 1) {
        this.reset();
        this.iServices.isrv_no = response.data.value;
        this.isNo = true;
      }else{
        this.message.fire("Error", response.message, "error");
      }
    })
  }

  save(saveForm:NgForm):void{
    debugger
    this.saving=true;
    let url_ = "/api/iservices/";
    if (!isNullOrUndefined(this._id)) {
      url_ = "/api/iservices/" + this._id;
      this.patch(url_, saveForm);
    }else{
      this.create(url_, saveForm);
    }
  }
  create(url_: string, saveForm:NgForm): void{
    this.service.shead_gl='609919427664d43524e9520c';
    this.iServices.isrv_heads.push(this.service);
    this.iServices.isrv_comp='609c01f4c645ce303c655ad2';
    this._processDataService.create(url_,this.iServices).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      console.log(response);
      this.notify.success("Saved Successfully","Success");
      saveForm.resetForm();
      this._id=null;
    })
  }
  patch(url_: string, saveForm:NgForm): void{
    this._processDataService.patch(url_,this.iServices).pipe(finalize(() => (this.saving=false))).subscribe(response=>{
      debugger
      console.log(response);
      this.notify.success("Update Successfully","Success");
      saveForm.resetForm();
      this._id=null;
    })
  }

  delete(): void {
    this.message.fire({
      title: 'Are you sure?',
      html: 'You are about to delete 1 record permanently.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel'
    }).then((result) => {
      if (result.value) {
        this._processDataService.delete("/api/iservices/" + this._id).subscribe(result => {
          this.notify.success('Deleted Successfully.', 'Deleted!');
          this.reset();
        });
      }
    });
  }

  reset():void{
    this.iServices= new iServicesDto();
    this._id=null;
  }
  setTypeheadList(param){
    switch (param) {
        case 'doc':
        this.typeheadlist=this.item
        break;
        case 'autotype':
        this.typeheadlist=this.autotype;
        break;
        case 'DocEvent':
        this.typeheadlist=this.docEvent;
        break;
        case 'invoiceType':
        this.typeheadlist=this.invoiceType;
        break;
        case 'docRule':
        this.typeheadlist=this.docRule;
        break;
        case 'automode':
        this.typeheadlist=this.autoMode;
        break;
    } 
  }

}


